<template>
    <transition name="slide">
        <div class="user-center">
            <div class="back">
                <div class="icon-back" @click="back"></div>
            </div>
            <div class="switches-wrapper">
                <Switches @switch="siwtchItem" :switches="switches" :currentIndex="currentIndex"></Switches>
            </div>
            <div class="play-btn">
                <i class="icon-play"></i>
                <span class="text" @click="randomPlaying">随机播放全部</span>
            </div>
            <div class="list-wrapper" ref="listWrapper">
                <scroll ref="favoriteList" v-if="currentIndex===0" class="list-scroll" :data="favoriteList">
                    <div class="list-inner">
                        <song-list :songs="favoriteList" @click="selectSong">
                        </song-list>
                    </div>
                </scroll>
                <scroll ref="playHistory" v-if="currentIndex===1" class="list-scroll" :data="playHistory">
                    <div class="list-inner">
                        <song-list :songs="playHistory" @click="selectSong">
                        </song-list>

                    </div>
                </scroll>
            </div>
            <div class="no-result-wrapper" v-show="noResult">
                <no-result :title="noResultDesc"></no-result>
            </div>
        </div>

    </transition>
</template>

<script>
import Switches from "widget/switch/switch";
import SongList from "widget/song-list/song-list";
import Scroll from "widget/scroll/scroll";
import NoResult from "widget/no-result/no-result";
import Song from "common/js/song";
import { playlistMixin } from "common/js/mixin";
import { mapGetters, mapActions } from "vuex";

export default {
  mixins: [playlistMixin],
  data() {
    return {
      currentIndex: 0,
      switches: [
        {
          name: "我喜欢的"
        },
        {
          name: "最新听的"
        }
      ]
    };
  },
  computed: {
    noResult() {
      if (this.currentIndex === 0) {
        return !this.favoriteList.length;
      } else {
        return !this.playHistory.length;
      }
    },
    noResultDesc() {
      if (this.currentIndex === 0) {
        return "暂无收藏歌曲";
      } else {
        return "你还没有听过歌曲";
      }
    },
    ...mapGetters(["favoriteList", "playHistory"])
  },
  components: { Switches, SongList, Scroll, NoResult },
  methods: {
    handlePlaylist(playlist) {
      const bottom = playlist.length > 0 ? "60px" : 0;
      this.$refs.listWrapper.style.bottom = bottom;
      this.$refs.favoriteList && this.$refs.favoriteList.refresh();
      this.$refs.playHistory && this.$refs.playHistory.refresh();
    },
    randomPlaying() {
      let list = this.currentIndex === 0 ? this.favoriteList : this.playHistory;

      if (list.length === 0) {
        return;
      }
      list = list.map(song => {
        return new Song(song);
      });

      this.randomPlay({
        list
      });
    },
    back() {
      this.$router.back();
    },
    selectSong(song) {
      this.insertSong(new Song(song));
    },
    siwtchItem(index) {
      this.currentIndex = index;
    },
    ...mapActions(["insertSong", "randomPlay"])
  }
};
</script>

<style lang="scss" scoped >
@import "~common/scss/variable";

.user-center {
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 100;
  width: 100%;
  background: $color-background;
  &.slide-enter-active,
  &.slide-leave-active {
    transition: all 0.3s;
  }
  &.slide-enter,
  &.slide-leave-to {
    transform: translate3d(100%, 0, 0);
  }
  .back {
    position: absolute;
    top: 0;
    left: 6px;
    z-index: 50;
    .icon-back {
      display: block;
      padding: 10px;
      font-size: $font-size-large-x;
    }
    color: $color-theme;
  }
  .switches-wrapper {
    margin: 10px 0 30px 0;
  }
  .play-btn {
    box-sizing: border-box;
    width: 135px;
    padding: 7px 0;
    margin: 0 auto;
    text-align: center;
    border: 1px solid $color-text-l;
    color: $color-text-l;
    border-radius: 100px;
    font-size: 0;
    .icon-play {
      display: inline-block;
      vertical-align: middle;
      margin-right: 6px;
      font-size: $font-size-medium-x;
    }
    .text {
      display: inline-block;
      vertical-align: middle;
      font-size: $font-size-small;
    }
  }
  .list-wrapper {
    position: absolute;
    top: 110px;
    bottom: 0;
    width: 100%;
    .list-scroll {
      height: 100%;
      overflow: hidden;
      .list-inner {
        padding: 20px 30px;
      }
    }
  }
  .no-result-wrapper {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
  }
}
</style>



